<template>
  <div class="record">
    <div class="head">
      <p>
        <i class="fa fa-trophy" aria-hidden="true"></i> Record</p>
    </div>
    <div class="data">
      <el-table v-loading="loading" :data="tableData" style="width: 100%" border height="500">
        <el-table-column prop="time"  label="日期" width="180" :formatter="dateFormat">
        </el-table-column>
        <el-table-column prop="score" label="获得分数" width="180">
        </el-table-column>
        <el-table-column prop="miles" label="奔跑路程" width="180">
        </el-table-column>
        <el-table-column prop="coins" label="获得R币" width="180">
        </el-table-column>
        <el-table-column prop="verify" label="校验hash">
        </el-table-column>
      </el-table>
      <el-row type="flex" justify="end">
        <el-button type="primary" @click="open"> 目前最高分 </el-button>
      </el-row>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.data {
  text-align: start;
  max-width: 1200px;
  margin: 120px auto;
  min-height: 500px;
}
.el-button {
  margin-top: 20px
}
</style>

<script>
import moment from 'moment'
import NebPay from 'nebpay'
const nebPay = new NebPay()
var dappAddress = 'n1koy3jwVv1RtZ3DkHTbH6VHWkZFT4aiNW3'
var netbase = 'https://mainnet.nebulas.io'
const api = function (callFunction, param = [], needPay = false) {
  return new Promise((resolve, reject) => {
    const to = dappAddress
    const value = '0'
    const callArgs = JSON.stringify(param)
    const way = needPay ? 'call' : 'simulateCall'
    nebPay[way](to, value, callFunction, callArgs, {
      listener: (res) => {
        console.log('listener', res)
        try{
          resolve(res)
        } catch (e) {
          reject(res)
        }
      }
    })
  })
}

export default {
  name: 'home',
  mounted () {
    api("getRecord", [], false).then(
        resp => {
          console.log('wisteria', resp)
          if (resp.execute_err === "") {
            this.tableData = JSON.parse(resp.result)
          }
          this.loading = false
        }
      ).catch(
        resp => {
          if (resp.execute_err === "") {
            this.tableData = JSON.parse(resp.result)
          }
          this.loading = false
        }
      )
  },
  methods: {
    dateFormat(row, column) {  
      var date = row[column.property];  
      if (date == undefined) {  
        return "";  
      }  
      return moment(date).format("YYYY-MM-DD HH:mm:ss");  
    },
    open() {
      api("getBestScore", [], false).then(
        resp => {
          if (resp.execute_err === "" && resp.result != "" && resp.result != "null") {
            var bextScore = JSON.parse(resp.result)
            const h = this.$createElement;
            this.$message({
              message: h('p', null, [
                h('span', null, '玩家: '),
                h('i', { style: 'color: teal' }, bextScore.player),
                h('span', null, '  分数: '),
                h('i', { style: 'color: teal' }, bextScore.bestScore)
              ])
            });
          }
        }
      ).catch(
        resp => {
          if (resp.execute_err === "" && resp.result != "" && resp.result != "null") {
            var bextScore = JSON.parse(resp.result)
            const h = this.$createElement;
            this.$message({
              message: h('p', null, [
                h('span', null, '玩家: '),
                h('i', { style: 'color: teal' }, bextScore.player),
                h('span', null, '  分数: '),
                h('i', { style: 'color: teal' }, bextScore.bestScore)
              ])
            });
          }
        }
      )
      
    }
  },
  data () {
    return {
      tableData: [],
      loading: true
    }
  }
}
</script>



