// Vuex状态管理
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    resetMenu: false
  },
  mutations: {
    handleresetMenu (state) {
      state.resetMenu = !state.resetMenu
    }
  }
})

export default store



